import TodayRoutine from "../components/TodayRoutine";
import { useEffect, useState } from "react";
import useAxiosGet from "../hook/useAxiosGet";
import useDecodingJwt from "../hook/useDecodingJwt";
import { Stack, Spinner, Container, Button, Row } from "react-bootstrap";

function Home() {
  const { myName } = useDecodingJwt();
  const [response, setResponse] = useState([]);
  const [routinesCount, setRoutinesCount] = useState(0);
  const [members, setMembers] = useState([]);
  const [memberName, setMemberName] = useState(myName);

  //   
  const { responseData, error, isLoading, refetch } = useAxiosGet({
    url: `/api/all/routines/today`,
  });
  useEffect(() => {
    if (!isLoading) {
      if (responseData !== null) {
        const memberList = [
          ...new Set(responseData.map((obj) => obj.memberName)),
        ].sort((a, b) => {
          if (a === myName) {
            return -1; //     
          } else if (b === myName) {
            return 1; //     
          } else {
            return 0; //    
          }
        });
        setMembers(memberList);

        const myData = responseData.filter((obj) => obj.memberName === myName);

        const filteredData = responseData.filter(
          (obj) => obj.memberName === memberName
        );

        let count = 0;
        for (const item of myData) {
          if (!item.complete) {
            count++;
          }
        }
        setRoutinesCount(count);
        const sortedResponseData = [...filteredData]; //   
        sortedResponseData.sort((a, b) => {
          // complete false   , true   
          if (a.complete === b.complete) {
            // complete    startTime 
            return a.startTime.localeCompare(b.startTime);
          } else {
            return a.complete ? 1 : -1; // false   
          }
        });
        setResponse(sortedResponseData);
        // console.log(sortedResponseData);
      } else {
        // console.log(error);
      }
    }
  }, [responseData, error, isLoading, memberName]);

  return (
    <>
      <Stack gap={1}>
        <Container>
          <Stack>
            <h4> {myName}!</h4>
            <p>   ~</p>
          </Stack>
        </Container>
        <Container>
          <Row className="justify-content-center">
            {isLoading ? (
              <Spinner animation="border" />
            ) : (
              <Container>
                {}
                {console.log(members)}
                <p>
                  {routinesCount === 0
                    ? "  .  !"
                    : `  ${routinesCount}  `}
                </p>
                {members.map((member) => (
                  <Button
                    key={member}
                    value={member}
                    onClick={() => setMemberName(member)}
                    style={{ marginRight: "4px", marginBottom: "4px" }}
                  >
                    {member === myName ? " " : member}
                  </Button>
                ))}
                {response.map((routine) => (
                  <TodayRoutine
                    key={routine.routineId}
                    routineId={routine.routineId}
                    routineName={routine.routineName}
                    memberName={routine.memberName}
                    strategy={routine.strategy}
                    certification={routine.certification}
                    startTime={routine.startTime}
                    endTime={routine.endTime}
                    complete={routine.complete}
                    setToReload={refetch}
                  />
                ))}
              </Container>
            )}
          </Row>
        </Container>
      </Stack>
    </>
  );
}

export default Home;
