import { useEffect, useState } from "react";
import SingleBtnModal from "./Modal/SingleBtnModal";
import useAxiosPost from "../hook/useAxiosPost";
import { Button, Container, Form, InputGroup, Stack } from "react-bootstrap";

function SignUp({ setIsMember }) {
  const [name, setName] = useState("");
  const [pw, setPw] = useState("");
  const [requestData, setRequestData] = useState("");
  const [successModalShow, setSuccessModalShow] = useState(false);
  const [errorModalShow, setErrorModalShow] = useState(false);

  //    input value 
  const changeMemberName = (e) => {
    setName(e.target.value);
  };

  //   
  const generatePassword = () => {
    const characters = "abcdefghijklmnopqrstuvwxyz";
    const randomChar = characters.charAt(
      Math.floor(Math.random() * characters.length)
    );
    const randomNum = Math.random().toString().substring(2, 6);
    const randomStr = randomChar + randomNum;
    setPw(randomStr);
  };

  // ,    json  
  const objToJson = () => {
    setRequestData(
      JSON.stringify({
        memberName: name,
        password: pw,
      })
    );
  };

  // json   
  const submitPost = (e) => {
    e.preventDefault();
    performPost();
  };
  const { responseData, error, isLoading, performPost } = useAxiosPost({
    url: `/api/auth/member/new`,
    requestData,
  });
  useEffect(() => {
    if (!isLoading) {
      if (responseData !== null) {
        setSuccessModalShow(true);
      } else {
        setErrorModalShow(true);
      }
    }
  }, [responseData, error, isLoading]);

  // ,   
  const isValid = name !== "" && pw !== "";

  //   
  const goToEnter = () => {
    setSuccessModalShow(false); //  
    setIsMember();
  };

  return (
    <Container className="d-flex flex-column justify-content-center align-items-center">
      <div>
        <span>Logo</span>
      </div>
      <h4>" !"</h4>
      <Form onSubmit={submitPost}>
        <Stack gap={3}>
          <InputGroup>
            <InputGroup.Text></InputGroup.Text>
            <Form.Control
              type="text"
              name="memberName"
              value={name}
              placeholder=" ."
              onChange={changeMemberName}
            ></Form.Control>
          </InputGroup>
          <InputGroup>
            <Button type="button" onClick={generatePassword}>
               
            </Button>
            <Form.Control
              type="text"
              value={pw}
              placeholder="  ."
              readOnly
            />
          </InputGroup>
          <p>:    ~</p>
          <Button
            type="submit"
            disabled={isValid ? false : true}
            onClick={objToJson}
          >
            
          </Button>
        </Stack>
      </Form>
      <Button type="button" variant="link" onClick={setIsMember}>
          
      </Button>
      <SingleBtnModal
        title={"."}
        content={"   ."}
        btnContent={""}
        show={successModalShow}
        onHide={goToEnter}
      />
      <SingleBtnModal
        title={" "}
        content={" ."}
        btnContent={""}
        show={errorModalShow}
        onHide={() => setErrorModalShow(false)}
      />
    </Container>
  );
}

export default SignUp;
