import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import PropTypes from "prop-types";
import SingleBtnModal from "./Modal/SingleBtnModal";
import useAxiosPost from "../hook/useAxiosPost";
import { Button, Container, Form, InputGroup, Stack } from "react-bootstrap";

function Login({ setIsMember }) {
  const [name, setName] = useState("");
  const [pw, setPw] = useState("");
  const [requestData, setRequestData] = useState("");
  const [errorModalShow, setErrorModalShow] = useState(false);
  const navigate = useNavigate();

  //    input value 
  const changeMemberName = (e) => {
    setName(e.target.value);
  };

  //    input value 
  const changeMemberPw = (e) => {
    setPw(e.target.value);
  };

  // ,   
  const isValid = name !== "" && pw !== "";

  // ,    json  
  const objToJson = () => {
    setRequestData(
      JSON.stringify({
        memberName: name,
        password: pw,
      })
    );
  };

  // json   
  const submitPost = (e) => {
    e.preventDefault();
    performPost();
  };
  const { responseData, error, isLoading, performPost } = useAxiosPost({
    url: `/api/auth/member`,
    requestData,
  });
  useEffect(() => {
    if (!isLoading) {
      if (responseData !== null) {
        sessionStorage.setItem("access-token", responseData.accessToken);
        navigate(`/home`);
      } else {
        setErrorModalShow(true);
      }
    }
  }, [responseData, error, isLoading]);

  return (
    <Container className="d-flex flex-column justify-content-center align-items-center">
      <div>
        <span>Logo</span>
      </div>
      <h4>"    ."</h4>
      <Form onSubmit={submitPost}>
        <Stack gap={3}>
          <InputGroup>
            <InputGroup.Text></InputGroup.Text>
            <Form.Control
              type="text"
              value={name}
              placeholder=" ."
              onChange={changeMemberName}
            />
          </InputGroup>
          <InputGroup>
            <InputGroup.Text></InputGroup.Text>
            <Form.Control
              type="password"
              value={pw}
              placeholder=" ."
              onChange={changeMemberPw}
            />
          </InputGroup>
          <Button
            type="submit"
            disabled={isValid ? false : true}
            onClick={objToJson}
          >
            
          </Button>
        </Stack>
      </Form>
      <Button type="button" variant="link" onClick={setIsMember}>
          
      </Button>
      <SingleBtnModal
        title={" "}
        content={"    ."}
        btnContent={""}
        show={errorModalShow}
        onHide={() => setErrorModalShow(false)}
      />
    </Container>
  );
}

Login.propTypes = {
  setIsMember: PropTypes.func.isRequired,
};

export default Login;
